.ant-menu.menu-home.dropdown.ant-menu-light {
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    -webkit-transform: translate3d(0, 0, 0);
}

.dropdown .ant-menu-item.ant-menu-item-active{
    background: rgba(0, 0, 0, 0.1) !important;
}
.dropdown .item-menu-recent-projects {
    font-size: 12px !important;
    cursor: default !important;
}
.dropdown .ant-menu-item {
    height: 40px;
    line-height: 20px;
    margin: 0px !important;
    padding: 10px 16px;
}
.dropdown .ant-menu-item:first-child,
.dropdown .ant-menu-item:nth-child(2) {
    color: var(--palette-purple);
}
.dropdown .ant-menu-item:nth-child(+n + 5) {
    color: var(--palette-beige) ;
}
.ant-menu.menu-home.dropdown.ant-menu-light {
    background-color: var(--palette-white) !important;
}
.ant-menu.menu-home.dropdown.ant-menu-dark {
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 8px rgba(255, 255, 255, 0.25);
    box-shadow: 0 2px 8px rgba(255, 255, 255, 0.25);
    -webkit-transform: translate3d(0, 0, 0);
}
.ant-menu.menu-home.dropdown.ant-menu-dark {
    background-color: var(--palette-gray-darken-4) !important;
    
}
.ant-menu.menu-home.dropdown.ant-menu-dark .ant-menu-item{
    color: var(--palette-gray) !important;
}
