.main-content {
  margin: 0px;
  padding: 10px ;
}
.main-layout {
  padding: 0px;
  margin: 0px;
  min-height: 100vh !important;
  
}
.main-layout.ant-layout  
.ant-menu{
  background-color: transparent !important;
}
/* Theme Light */
.main-layout.light.ant-layout,
.main-layout.light.ant-layout  .ant-layout-footer,
.main-layout.light.ant-layout > section.ant-layout {
  background-color: var(--palette-gray) !important;

}

.main-layout.light.ant-layout > section.ant-layout .ant-layout-header{
  background-color: var(--palette-purple) !important;

}
/*Layou Sider*/
.main-layout.light.ant-layout >
aside.ant-layout-sider {
  background: var(--palette-gray-darken-2);
}

.main-layout.light.ant-layout >
aside.ant-layout-sider .menu-sider .ant-menu-item.ant-menu-item-selected{
  background: var(--palette-yellow) !important;
}
.main-layout.light.ant-layout >
aside.ant-layout-sider .menu-sider .ant-menu-item-active:hover {
  background: var(--palette-yellow) !important;
}
.main-layout.light.ant-layout >
aside.ant-layout-sider .menu-sider li.ant-menu-item {
  background: var(--palette-gray-darken-1);
  margin-bottom: 2px !important;
  
}


/* Theme Dark */
.main-layout.dark.ant-layout,
.main-layout.dark.ant-layout  .ant-layout-footer,
.main-layout.dark.ant-layout > section.ant-layout {
  background-color: var(--palette-gray-darken-3) !important;
  color: var(--palette-gray-2) !important;
}

.main-layout.dark.ant-layout >  .header-home .ant-layout-header{
  background-color: var(--palette-gray-darken-4) !important;

}

/*Layou Sider*/
.main-layout.dark.ant-layout >
aside.ant-layout-sider {
  background: var(--palette-gray-darken-4);
}
.main-layout.dark.ant-layout >
aside.ant-layout-sider .menu-sider .ant-menu-item-selected{
  background: var(--palette-gray-darken-1) !important;
  color: var(--palette-yellow);
}
.main-layout.dark.ant-layout >
aside.ant-layout-sider .menu-sider li.ant-menu-item {
  background: var(--palette-gray-darken-3) ;
}
.main-layout.dark.ant-layout >
aside.ant-layout-sider .menu-sider .ant-menu-item-active:hover {
  background: var(--palette-gray-darken-1);
  color: var(--palette-yellow);
}





/* Value standar out theme */
.logo {
  margin: 0 !important;
  padding: 20px 40px 40px 20px;
  color: white;
  font-size: 24px;
  line-height: 24px;
  background: var(--palette-gray-darken-3);
}

.logo img {
  height: auto;
  width: 100%;
}
.ant-layout-sider-collapsed .logo-title {
  display: none !important;
}
.logo-title {
  padding-left: 60px;
  font-size: 18px;
  line-height: 18px;
}
aside.ant-layout-sider .menu-sider .ant-menu-item{
  margin: 0 !important;
}
aside.ant-layout-sider .menu-sider li.ant-menu-item {
  margin-bottom: 2px !important;
}
.main-layout.ant-layout >
aside.ant-layout-sider .menu-sider li.ant-menu-item i {
  background: var(--palette-white);
  color: var(--palette-gray-darken-4);
}
.main-layout.ant-layout >
aside.ant-layout-sider .menu-sider li.ant-menu-item-selected i {
  background: var(--palette-gray-darken-3) !important;
  color: var(--palette-white);
}
.main-layout.ant-layout >
aside.ant-layout-sider .menu-sider .ant-menu-item-active:hover > i {
  background: var(--palette-gray-darken-3) !important;
  color: var(--palette-white) ;
}

.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: 1px solid transparent !important;
}

.menu-sider li.ant-menu-item i {
  font-size: 18px !important;
  vertical-align: middle;
  height: 40px;
  width: 40px;
  padding: 8px;
  font-size: 24px !important;
  border-radius: 50%;
  text-align: center;
}
.ant-layout-sider-collapsed .menu-sider li.ant-menu-item i {
  transform: translate(-10px, 0px) !important;
  padding: 0px !important;
}

.ant-layout-sider {
  width: 250px !important;
  flex: 0 0 250px !important;
  max-width: 250px !important;
  min-width: 250px !important;
}
.ant-layout-sider .ant-layout-sider-trigger {
  width: 250px !important;
}
.ant-layout-sider-collapsed .ant-layout-sider-trigger {
  width: 80px !important;
}
.ant-layout-sider-collapsed {
  width: 80px !important;
  flex: 0 0 80px !important;
  max-width: 80px !important;
  min-width: 80px !important;
}

