.config-container,
.container {
    margin: 0 auto;
    max-width: 1280px;
    //background-color: aquamarine;
}
.config-container{
    width: 100%;
    transform: translateY(-150px);
}
.container{
    width: 90%;
}
@media only screen and (min-width: 601px) {
    .container {
        width: 85%;
    }
    .config-container{
        width: 85%;
    }
}
@media only screen and (min-width: 993px) {
    .container {
        width: 70%;
    }
    .config-container{
        width: 70%;
    }
}

.config-title{
    font-weight: bold;
    font-size: 28px;
    padding: 0 10px;
    color: var(--palette-white);
}
Button, input{
    border-radius: 50px !important;
}
.ant-btn .btn,
Button.btn{
    line-height: 0.5 !important;
    padding: 10px 30px !important;
}
input{
    margin: 10px 0 !important;
}

.card-button,
button.card-button{
    margin: 10px;
    border-radius: 10px !important;
}
button.card-button{
    height:150px !important;
    width: 150px !important;
}
div.card-button{
    display: block;
    float: left;
    width: 200px !important;
}
.card-button > .card-container{
    padding: 10px;
}
.card-button > .card-container .card-title{
    text-align: center;
    font-size: 1.5em;
    padding: 10px 5px 0 5px;
}
.card-button > .anticon,
.card-button > .card-container .anticon {
    width: 50px;
    height: 50px;
    display: block !important;
    margin: 0 auto;
    padding: 10px;
    border-radius: 50px;
    font-size: 2em !important;
}
.card-button > span {
    margin: 10px 0;
}
.card-button > .card-footer,
.card-login > .card-footer{
    min-height: 50px;
    width: 100%;
    display: block;
    padding: 10px;
}
.card-footer > .footer-row{
    width: 100%;
    display: block;
    padding: 2px;
}
.card-footer > .footer-row .anticon {
    margin: 0 5px;
    padding: 5px;
    border-radius: 50px;
    font-size: 1em !important;
}

.card-login {
    height: auto;
    max-width: 300px;
    margin: calc(100px) auto ;
    border-radius: 10px;
}
.card-login > .card-container{
    margin: 10px 30px
}
.card-login > .card-container img{
    height: auto;
    width: 80px;
    margin: 15px auto;
    display: block;
}

.card-login > .card-container Button{
    width: 100%;
    margin-top: 50px;
} 

.card-login > .card-footer  Button {
    display: block;
    margin: 10px auto;
    height: 70px;
    width: 70px;
}
.card-login > .card-footer  Button .anticon{
    font-size: 2em !important;
}

.card-database {
    border-radius: 10px;
}
.card-database > .card-header{
    padding: 20px 40px;
}
.chip i.anticon {
    height: 25px;
    width: 25px;
    padding: 5px;
    border-radius: 50px;
    transform: translateX(-12px);
    color: var(--palette-white);
}
/* Theme Light */
.main-layout.light.ant-layout .card-database .card-container .title{
    font-weight: 300;
    color: var(--palette-purple) !important;
}
.main-layout.light.ant-layout .card-database,
.main-layout.light.ant-layout .card-button,
.main-layout.ant-layout  .card-login {
    background-color: var(--palette-white);
    color: var(--palette-purple);
}
.main-layout.light.ant-layout .card-database .card-container .title{
    font-weight: 300;
    color: var(--palette-white);
}
.main-layout.light.ant-layout button.card-button,
.main-layout.light.ant-layout .card-database,
.main-layout.light.ant-layout .card-button,
.main-layout.light.ant-layout  .card-login{
    border: 0px solid var(--palette-purple) !important;
}

.main-layout.light.ant-layout  .card-button > .card-footer,
.main-layout.light.ant-layout .card-database > .card-footer,
.main-layout.light.ant-layout  .card-login  .card-footer{
    border-top: 1px solid var(--palette-purple);
}
.main-layout.light.ant-layout .card-database > .card-header{
    border-bottom: 1px solid var(--palette-purple);
}

.main-layout.light.ant-layout .card-button > .anticon,
.main-layout.light.ant-layout .card-button > .card-container .anticon,
.main-layout.light.ant-layout .card-button > .card-footer > .footer-row .anticon {
    background-color: var(--palette-yellow) !important;
    color: var(--palette-purple);
}

.main-layout.light.ant-layout .card-login > .card-footer  Button{
    background-color: var(--palette-white) !important;
    color: var(--palette-purple);
}
.main-layout.light.ant-layout input{
    background-color: var(--palette-white);
    border: 1px solid var(--palette-purple);
    color: var(--palette-purple);
}
.main-layout.light.ant-layout .chip{
    background-color: var(--palette-white);
    border: 1px solid var(--palette-gray-2);
    color: var(--palette-gray-1) ;
}
.main-layout.light.ant-layout .chip i.anticon {
    background-color: var(--palette-gray-2);
}

.main-layout.light.ant-layout .chip.chip-active{
    background-color: var(--palette-gray);
    border: 0px solid var(--palette-gray-2);
    color: var(--palette-orange);
}
.main-layout.light.ant-layout .chip.chip-active i.anticon {
    background-color: var(--palette-orange);
}

.main-layout.light.ant-layout .chip.chip-off{
    background-color: var(--palette-white);
    border: 1px solid var(--palette-gray);
    color: var(--palette-gray);
}
.main-layout.light.ant-layout .chip.chip-off i.anticon {
    background-color: var(--palette-gray-1);
}
.main-layout.light.ant-layout .chip.chip-on{
    background-color: var(--palette-white);
    border: 1px solid var(--palette-purple);
    color: var(--palette-purple);
}
.main-layout.light.ant-layout .chip.chip-on i.anticon {
    background-color: var(--palette-purple);
}

.main-layout.light.ant-layout .ant-btn .btn:hover,
.main-layout.light.ant-layout Button.btn:hover{
    background-color: var(--palette-orange);
    color: var(--palette-gray-1);
}
/* Theme Dark */
.main-layout.dark.ant-layout .card-database,
.main-layout.dark.ant-layout .card-button {
    background-color: var(--palette-gray-darken-2);
    color: var(--palette-white);
}
.main-layout.dark.ant-layout .card-database .card-container .title{
    font-weight: 300;
    color: var(--palette-white);
}
.main-layout.dark.ant-layout button.card-button,
.main-layout.dark.ant-layout .card-database,
.main-layout.dark.ant-layout .card-button,
.main-layout.dark.ant-layout  .card-login{
    border: 1px solid var(--palette-gray-darken-4) !important;
}

.main-layout.dark.ant-layout  .card-button > .card-footer,
.main-layout.dark.ant-layout .card-database > .card-footer,
.main-layout.dark.ant-layout  .card-login  > .card-footer{
    border-top: 1px solid var(--palette-gray-darken-4);
}
.main-layout.dark.ant-layout .card-database > .card-header{
    border-bottom: 1px solid var(--palette-gray-darken-4);
}

.main-layout.dark.ant-layout .card-button > .anticon,
.main-layout.dark.ant-layout .card-button > .card-container .anticon,
.main-layout.dark.ant-layout .card-login > .card-footer  Button,
.main-layout.dark.ant-layout .card-button > .card-footer > .footer-row .anticon {
    background-color: var(--palette-gray-darken-3) !important;
    color: var(--palette-white);
}

.main-layout.dark.ant-layout .onTheme input,
.main-layout.dark.ant-layout .chip{
    background-color: var(--palette-gray-darken-2);
    border: 1px solid var(--palette-gray-darken-4);
    color: var(--palette-white) ;
}

.main-layout.dark.ant-layout .chip i.anticon {
    background-color: var(--palette-gray-3);
}
.main-layout.dark.ant-layout .chip.chip-active{
    background-color: var(--palette-gray-darken-4);
    border: 0px solid var(--palette-gray-2);
    color: var(--palette-white);
}
.main-layout.dark.ant-layout .chip.chip-active i.anticon {
    background-color: var(--palette-gray-darken-1);
}

.main-layout.dark.ant-layout .chip.chip-off{
    background-color: var(--palette-gray-darken-1);
    border: 1px solid var(--palette-gray-darken-4);
    color: var(--palette-gray-1);
}

.main-layout.dark.ant-layout .chip.chip-off i.anticon {
    background-color: var(--palette-gray-2);
}
.main-layout.dark.ant-layout .chip.chip-on{
    background-color: var(--palette-gray-darken-2);
    border: 1px solid var(--palette-gray-darken-4);
    color: var(--palette-gray-darken-4);
}

.main-layout.dark.ant-layout .chip.chip-on i.anticon {
    background-color: var(--palette-gray-darken-4);
}

.main-layout.dark.ant-layout .ant-btn .btn,
.main-layout.dark.ant-layout Button.btn{
    background-color: var(--palette-gray-darken-4);
    border: 0px solid ;
}

.main-layout.dark.ant-layout .ant-btn .btn:hover,
.main-layout.dark.ant-layout Button.btn:hover{
    background-color: var(--palette-gray-darken-3);
    color: var(--palette-gray-2);
}